// import { Link } from 'gatsby';

import Layout from '../components/layout';
import Container from '../components/Container';
import Row from '../components/Row';

import './index.css';

const SpeakerPost = (props) => {
	const fullName = props.fullName;
	const imageSrc = props.imageSrc;
	const linkTo = props.linkTo;
	const speakerTitle = props.speakerTitle;
	const speakerPosition = props.speakerPosition;
	const contentTitle = props.contentTitle;
	return (
		<div className="speaker-post">
			<a href={linkTo} target="_blank" rel="noopener noreferrer">
				<img src={imageSrc} alt={`${speakerTitle} ${fullName}`} />
			</a>
			<div className="speaker-content">
				<h2>
					<a href="#" title={`${speakerTitle} ${fullName}`} target="_blank" rel="noopener noreferrer">
					{/* <a href="#" title="Dr.-Ing. Jens Lambrecht, Founder"> */}
						{`${speakerTitle} ${fullName}`}
					</a>
				</h2>
				<span>{`${speakerTitle} ${fullName}, ${speakerPosition}`}</span>
				<p>{contentTitle}</p>
				<ul className="social-links">
					<li><a className="facebook" href="#"><i className="fa fa-facebook"></i></a></li>
					<li><a className="twitter" href="#"><i className="fa fa-twitter"></i></a></li>
					<li>
						<a className="linkedin" target="_blank" rel="noopener noreferrer" href={linkTo}>
							LinkedIn
						</a>
					</li>
					{/* <li><a className="linkedin" href="#"><i className="fa fa-linkedin"></i></a></li> */}
					<li><a className="personal-web" href="#"><i className="fa fa-link"></i></a></li>
				</ul>
			</div>
		</div>
	);
}

const CopyrightContainer = ({ children, data }) => (
  <StaticQuery
		query={graphql`
		query SiteCopyrightQuery {
      site {
        siteMetadata{
					title
					rssMetadata {
						copyright
					}
        }
      }
    }
  `}
    render={data => (
			<React.Fragment>
				<p className="copyright-line">{data.site.siteMetadata.rssMetadata.copyright}</p>
      	{/* <p className="copyright-line">&copy; Copyright {data.site.siteMetadata.title}</p> */}
			</React.Fragment>
		)}
  />
);

